.form-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.connect {
  width: 300px;
  height: auto;
  margin: 100px 0px 60px 0px;
}

form h2 {
  margin-bottom: 20px;
}
input,
textarea {
  border: none;
  border-bottom: 1px solid black;
  outline: none;
  margin-bottom: 30px;
  width: 100%;
  max-width: 500px;
  background-color: black;
  color: white;
}
textarea {
  height: 50px;
  max-width: 360px;
  min-width: 100px;
}
form button {
  font-size: 1.4rem;
  display: block;
  border-radius: 30px;
  padding: 10px 50px;
  border: none;
  background-image: linear-gradient(
    135deg,
    gold,
    pink,
    #4158d0,
    #c850c0,
    #ffcc70,
    #0093e9,
    #80d0c7,
    #ff0000,
    #fc00ff
  );
  background-size: 500%;
  background-position: left;

  animation: change-color 10s ease-in-out infinite forwards;
}
@media (min-width: 900px) {
  .form-con-sec {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 80%;
  }
  .connect {
    align-self: center;
  }
  .name-and-email-container {
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 5%;
  }
  .name-and-email-container div {
    width: 80%;
  }
  form button {
    align-self: center;
    justify-self: center;
  }
  .message-con {
    width: 50%;
  }
  .button-con {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  textarea {
    max-width: 500px;
  }
}
