.all-nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 10px;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: black;
  z-index: 10;
}

.bobby-logo {
  width: 120px;
  height: auto;
  fill: transparent;
  animation: to-fill 2s ease forwards 3.8s;
}
.bobby-mask {
  animation: to-fill 2s ease forwards 3.8s;
}

.bobby-logo path:nth-child(2) {
  stroke-dasharray: 1582;
  stroke-dashoffset: 1582;
  animation: line-animate 3s ease forwards;
}
.bobby-logo path:nth-child(3) {
  stroke-dasharray: 428;
  stroke-dashoffset: 428;
  animation: line-animate 3s ease forwards 0.3s;
}
.bobby-logo path:nth-child(4) {
  stroke-dasharray: 702;
  stroke-dashoffset: 702;
  animation: line-animate 3s ease forwards 0.6s;
}
.bobby-logo path:nth-child(5) {
  stroke-dasharray: 702;
  stroke-dashoffset: 702;
  animation: line-animate 3s ease forwards 0.9s;
}
.bobby-logo path:nth-child(6) {
  stroke-dasharray: 883;
  stroke-dashoffset: 883;
  animation: line-animate 3s ease forwards 1.2s;
}
@keyframes line-animate {
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes to-fill {
  to {
    fill: white;
  }
}

path {
  stroke: white;
}
