.welcome {
  width: 350px;
  height: auto;
  margin: 40px 0;
}
.hero-text-con {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.text-only-con {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.intro-text-con {
  width: 85%;
  max-width: 500px;
  display: flex;
  gap: 15px;
}
.intro-text-con::before {
  content: "";
  display: block;
  width: 15px;
  height: 295px;
  background-image: linear-gradient(
    100deg,
    gold,
    pink,
    #4158d0,
    #c850c0,
    #ffcc70,
    #0093e9,
    #80d0c7,
    #ff0000,
    #fc00ff
  );
  background-size: 300%;
  background-position: left;
  transition: 2s ease-in-out;
  border-radius: 25px;
  animation: change-color 10s ease-in-out infinite forwards;
}
.beautiful {
  background-image: linear-gradient(
    100deg,
    gold,
    pink,
    #4158d0,
    #c850c0,
    #ffcc70,
    #0093e9,
    #80d0c7,
    #ff0000,
    #fc00ff
  );
  color: transparent;
  background-clip: text;
  background-size: 300%;
  background-position: left;
  transition: 2s ease-in-out;
  animation: change-color 10s ease-in-out infinite forwards;
}
@keyframes change-color {
  0% {
    background-position: left;
  }
  50% {
    background-position: right;
  }
  100% {
    background-position: left;
  }
}
.intro-text-con h1 {
  font-size: 2.2rem;
  padding-bottom: 15px;
}
.bobby-bracket {
  position: relative;
}
.text-block {
  content: "";
  display: block;
  width: 150px;
  height: 58px;
  z-index: 4;
  background-color: white;
  position: absolute;
  top: 0;
  left: -10px;
  background-color: black;
  border-left: 4px solid #2c3e50;
}
.intro-text-con p {
  font-size: 1.7rem;
  line-height: 40px;
  padding-bottom: 15px;
}
.cartoon-background {
  width: 80%;
  opacity: 1;
  height: 300px;
  max-width: 450px;
  border-radius: 50%;
  background-image: linear-gradient(
    45deg,
    gold,
    pink,
    #4158d0,
    #c850c0,
    #ffcc70,
    #0093e9,
    #80d0c7,
    #ff0000,
    #fc00ff
  );
  background-size: 300%;
  background-position: left;

  position: relative;
  animation: change-color 10s ease-in-out infinite forwards;
}
.cartoon-background::before {
  content: "";
  display: block;
  border-radius: 50%;
  width: 90%;
  height: 270px;
  position: absolute;
  transform: translate(6%, 5%);
  background: linear-gradient(50deg, #bdc3c7, #2c3e50);
}
.cartonMe {
  width: 90%;
  height: 250px;
  position: absolute;
  transform: translate(5%, 20%);
}

@media (min-width: 900px) {
  .hero-text-con {
    padding: 0px 40px;
    flex-direction: row;
    justify-content: space-around;
  }
  .welcome {
    align-self: start;
  }
  .intro-text-con {
    align-self: start;
  }
}
