.footer-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}
.details-container {
  display: flex;
  gap: 40px;
}
.details-container h3 {
  margin-bottom: 10px;
}
.details-container::before {
  content: "";
  display: block;
  width: 10px;
  height: 100px;
  background-image: linear-gradient(
    100deg,
    gold,
    pink,
    #4158d0,
    #c850c0,
    #ffcc70,
    #0093e9,
    #80d0c7,
    #ff0000,
    #fc00ff
  );
  background-size: 500%;
  background-position: left;

  animation: change-color 10s ease-in-out infinite forwards;
  border-radius: 25px;
}
.social-container {
  width: 90%;
  margin-top: 20px;
  margin-bottom: 160px;
  display: flex;
  justify-content: space-around;
}
.social-logo {
  width: 35px;
  height: auto;
}

.original-background {
  width: 80%;
  height: 300px;
  max-width: 370px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  background-image: linear-gradient(
    45deg,
    gold,
    pink,
    #4158d0,
    #c850c0,
    #ffcc70,
    #0093e9,
    #80d0c7,
    #ff0000,
    #fc00ff
  );
  background-size: 300%;
  background-position: left;

  animation: change-color 10s ease-in-out infinite forwards;
}

.original-background::before {
  content: "";
  display: block;
  border-radius: 50%;
  width: 90%;
  height: 270px;
  position: absolute;
  transform: translate(6%, 5%);
  background: linear-gradient(50deg, #bdc3c7, #2c3e50);
}
.author {
  width: 70%;
  height: 250px;
  position: absolute;
  transform: translate(20%, 40%);
}
@media (min-width: 900px) {
  .footer-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 200px 100px;
    justify-content: center;
    align-items: center;
  }
  .original-background {
    width: 290px;
    height: 190px;
    justify-self: center;
  }
  .details-container {
    justify-self: center;
  }
  .original-background::before {
    height: 170px;
  }
  .author {
    width: 70%;
    height: 250px;
    transform: translate(20%, 10%);
  }
  .social-container {
    grid-column: 1/3;
    margin-top: 20px;
    align-self: start;
    order: 3;
  }
}
