.all-project-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  position: relative;
}
.card-container {
  width: 100%;
  height: 380px;
  display: grid;
  grid-template-columns: repeat(4, 95%);

  overflow-x: hidden;
}
.card {
  width: 95%;
  height: 280px;
  position: relative;
}
.card::after {
  z-index: 10;
  background: white;
  text-align: center;
  position: absolute;
  bottom: 30px;
  left: 0;
  width: 0%;
  transition: 1s;
  overflow: hidden;
  color: black;
}
.first::after {
  content: "Nike Project";
}
.second::after {
  content: "Crypto Project";
}
.third::after {
  content: "Country Api Project";
}
.fourth::after {
  content: "Space Exploration ";
}
.card-pic {
  width: 100%;
  height: 380px;
}
.card-pic:hover {
  filter: blur(4px);
}
.card:hover:after {
  width: 100%;
}
.projects {
  width: 300px;
  height: auto;
  margin: 100px 0px 60px 0px;
}
.downloadCv {
  padding: 15px 30px;
  font-size: 1.3rem;
  background-color: white;
  margin-top: 30px;
  color: black;
}

@media (min-width: 900px) {
  .all-project-container {
    justify-content: center;
    margin-bottom: 700px;
  }
  .card-container {
    overflow-x: visible;
    grid-template-columns: repeat(2, 40%);
    grid-template-rows: repeat(2, 500px);
    max-width: 2000px;
    justify-content: center;
    align-items: center;
  }
  .card {
    left: 0;
    height: 380px;
    max-width: 600px;
  }
  .downloadCv {
    position: absolute;
    bottom: -700px;
  }
}
